body{background:#FAFAFA
}
p,ul,ol,quote{margin-top:24px;margin-bottom:0;font-size:17px;font-weight:400;line-height:32px;color:#646C77;display:block
}
p a,ul a,ol a,quote a{color:#656c76;text-decoration:none;border-bottom:1px dotted #646C77;transition:all 0.3s
}
img{max-width:100%;height:auto
}
html{font-family:"Lora",serif
}
h2{margin-top:40px;margin-bottom:20px;font-size:30px;font-style:normal;font-weight:500;display:block;font-family:"Roboto",sans-serif
}
.post .header .title{margin-top:40px;margin-bottom:20px;font-size:25px;font-style:normal;font-weight:500;display:block
}
.post .header .title a{font-family:"Roboto",sans-serif;color:#4a525f;text-decoration:none;border-bottom:1px dotted rgba(74,82,95,0.4);transition:all 0.2s ease-in-out
}
.post .header time{margin:0;font-size:14px;font-style:normal;font-weight:600;line-height:20px;text-transform:uppercase;color:#646c77
}
.post .content h2{font-size:20px;color:#4a525f;text-decoration:none;transition:all .2s ease-in-out
}
.fade-enter-active,.fade-leave-active{transition:opacity .3s ease
}
.fade-enter,.fade-leave-active{opacity:0
}
.slide-left-enter,.slide-right-leave-active{opacity:0
}
.slide-left-leave-active,.slide-right-enter{opacity:0
}
#app{display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;min-height:100vh
}
#app .top{-ms-flex:1;flex:1
}



